.weather-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-content: flex-start;
}
.setting-container {
  width: 100%;
  height: 1.5rem;
  display: flex;
  justify-content: flex-end;
}

.ww-container {
  width: 20rem;
  margin: 0.5rem;
}
.ww-title {
  width: 100%;
  text-align: left;
}
.ww-temperature {
  width: 100%;
  display: flex;
  justify-content: center;
  .ww-temperature-img {
    width: 50%;
  }
  .ww-temperature-deg {
    width: 50%;
  }
}
